body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.hello-page h1{
    font-size: 80px;
}
.hello-page h6{
    font-family: Cambria;
    font-size: 24px;
}

.hello-page h2{
    color: blue;
    font-size: 64px;
}
div {
    width: 100%;
    font-size: 18px;
    margin: 0;
    padding: 0;
}

table {
    margin: auto;
}
div {
    text-align: center;
}

li {
    list-style: none;
}

a {
    color: black;
}
.App {
    text-align: center;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: stretch;
            align-items: stretch;
    height: 100vh;
    background-image: url(/static/media/IMG_4207.3c01f85d.JPG);
    background-position: center;
    background-size: cover;

}

.main-page {

    width: 20%;
    min-width: 220px;
    padding-right: 15px;
    padding-bottom: 10px;
    background-color: rgba(126, 235, 164, 0.1);
}

button {

    outline: none;
    padding: 10px 20px;
    border-radius: 10px;
    background-image: -webkit-linear-gradient(left, #9EEFE1 0%, #4830F0 51%, #9EEFE1 100%);
    background-image: linear-gradient(to right, #9EEFE1 0%, #4830F0 51%, #9EEFE1 100%);
    background-size: 200% auto;
    box-shadow: 0 0 20px rgba(0,0,0,.1);
    -webkit-transition: .5s;
    transition: .5s;

}
.btn-page {
    background: -webkit-linear-gradient(left, rgba(191, 195, 241, 0.3),rgba(23, 41, 241, 0.5) 50%,rgba(142, 151, 250, 0.3) 100%);
    background: linear-gradient(90deg, rgba(191, 195, 241, 0.3),rgba(23, 41, 241, 0.5) 50%,rgba(142, 151, 250, 0.3) 100%);
}



